export default class EquipmentBag {
  equipmentList: {
    [type: string]: {
      eqp_id: number,
      name: string,
      equippedBy: number | null
    }[]
  };

  equipmentItem: {
    [key: string]: {
      eqp_id: number,
      type: string,
      name: string,
      equippedBy: number | null
    }
  };

  constructor(equipmentData: { eqp_id: number, type: string, name: string, equippedBy: number | null }[]) {
    this.equipmentList = {};
    this.equipmentItem = {};
    for (let equipment of equipmentData) {
      this.addEquipment(equipment.eqp_id, equipment.type, equipment.name, equipment.equippedBy);
    }
  }

  // 装備を追加するメソッド
  addEquipment(eqp_id: number, type: string, name: string, equippedBy: number | null) {
    if (!this.equipmentList[type]) {
      this.equipmentList[type] = [];
    }
    const equipment = { eqp_id: eqp_id, type: type, name: name, equippedBy: equippedBy };
    this.equipmentList[type].push(equipment);
    const key = `${type}_${equippedBy === null ? 'null' : equippedBy}`;
    this.equipmentItem[key] = equipment;
  }
  
  // 装備を削除するメソッド
  removeEquipment(eqp_id: number) {
    let equipments = this.equipmentList[eqp_id];
    if (!equipments || equipments.length === 0) {
      throw new Error(`装備 ${eqp_id} は存在しません。`);
    }
    equipments.pop();
  }

  // // 特定の装備を検索するメソッド
  // findEquipment(eqp_id: number): { eqp_id: number, equippedBy: number | null } | undefined {
  //   let equipments = this.equipmentList[eqp_id];
  //   if (!equipments || equipments.length === 0) {
  //     return undefined;
  //   }
  //   return equipments[equipments.length - 1];
  // }

  // 指定したtypeの装備リストを取得するメソッド
  getEquipmentByType(type: string) {
    return this.equipmentList[type] || [];
  }

  // eqp_idとequippedByを指定して装備を取得するメソッド
  findEquipment(eqp_id: number, cha_id: number | null) {
    const key = `${eqp_id}_${cha_id === null ? 'null' : cha_id}`;
    return this.equipmentItem[key];
  }

  // 装備を装着するメソッド
  equipEquipment(eqp_id: number, cha_id: number) {
    const key = `${eqp_id}_null`;
    const equipment = this.equipmentItem[key];
    if (!equipment) {
      throw new Error(`装備 ${eqp_id} は存在しません。`);
    }
    equipment.equippedBy = cha_id;
    this.equipmentItem[`${eqp_id}_${cha_id}`] = equipment;  // 新しいキーで装備を追加
    delete this.equipmentItem[key];  // 元のキーを削除
  }

  // 装備を装備解除するメソッド
  unequipEquipment(eqp_id: number, cha_id: number) {
    const key = `${eqp_id}_${cha_id}`;
    const equipment = this.equipmentItem[key];
    if (!equipment) {
      throw new Error(`装備 ${eqp_id} はキャラクター ${cha_id} に装着されていません。`);
    }
    equipment.equippedBy = null;
    this.equipmentItem[`${eqp_id}_null`] = equipment;  // 新しいキーで装備を追加
    delete this.equipmentItem[key];  // 元のキーを削除
  }
}