<template>
  <div class="UI">
    <div v-if="status == 'Opening'">
      <Opening @updateStatus="updateStatus" />
    </div>
    <div v-if="status == 'Normal'">
      <CurrentUI class="CurrentUI" :characters="party.characters" v-show="showUIstatus" />
      <PartyUI class="PartyUI" :characters="party.characters" @toggleUIup="toggleUIup" />
      <Dungeon @updatePosition="updatePosition" :showUIstatus="showUIstatus" />
      <!-- <BagUI class="BagUI" /> -->
    </div>
  </div>
</template>

<script setup lang="ts">

import { ref, provide } from 'vue';
import * as THREE from 'three';

import Opening from './Process/Opening.vue';
import Dungeon from './Process/Dungeon.vue';
import { MapData } from './Process/CreateDungeon.ts';
import CurrentUI from './UI/Current.vue';
import PartyUI from './UI/Party.vue';

// import Config from './config.ts';
import Party from './Class/Party.ts';

///////////////////////////////
//初期設定
///////////////////////////////

//状態管理
const status = ref('Opening')
const updateStatus = (newStatus: string) => {
  status.value = newStatus;
};

const showUIstatus = ref(true)
const toggleUIup = (status: boolean) => {
  showUIstatus.value = status;
};

//プレイヤー現在地
let playerPosition = new THREE.Vector3();
const updatePosition = (newPosition: THREE.Vector3) => {
  playerPosition = newPosition;
};

// Partyクラスのインスタンスを作成
let partyData = {
  gold: 100000,
  location: [570, 180],
  home: [500, 200],
  map: MapData,
};
let party = ref(new Party(partyData));
provide('party', party);
console.log(party)

</script>

