<template>
  <canvas ref="MapUI"></canvas>
</template>
  
<script setup lang="ts">
import { ref, PropType, watch, Ref, nextTick } from 'vue';
import * as THREE from 'three';
import Config from '@/config.ts';

//素材import
import img_Position from '/img/icon/playerPosition.png';

const props = defineProps({
  MapData: Array as PropType<number[][]>,
  playerPosition: THREE.Vector3
})

const MapUI: Ref<HTMLCanvasElement | undefined> = ref();
const playerIcon = new Image();
playerIcon.src = img_Position;

let prevPlayerPosition: THREE.Vector3 | null = null;

// ミニマップを描画
const initMapUI = () => {

  if (props.MapData!.length == 0) return;
  if (!MapUI.value) return;
  const ctx = MapUI.value?.getContext('2d');
  if (!ctx) return;
  MapUI.value.height = Config.MiniHeight

  for (let i = 0; i < props.MapData!.length; i++) {
    for (let j = 0; j < props.MapData![i].length; j++) {
      switch (props.MapData![i][j]) {
        case 0: // 壁
          ctx.fillStyle = Config.MiniWallColor;
          break;
        case 1: // 部屋                
        case 2: // 通路
          ctx.fillStyle = Config.MiniRoomColor;
          break;
      }
      ctx.fillRect(j * 3, i * 3, 3, 3);
    }
  }
};

// ミニマップを更新
const playerMove = () => {

  const ctx = MapUI.value?.getContext('2d');
  if (!ctx) return;
  if (!props.playerPosition) return;
  // プレイヤーの描画
  // 前のプレイヤーの位置
  if (prevPlayerPosition) {
    ctx.fillStyle = Config.MiniMoveColor;
    ctx!.fillRect(Math.floor(prevPlayerPosition.x / Config.BlockSize - 1) * 3, Math.floor(prevPlayerPosition.z / Config.BlockSize - 1) * 3, Config.BlockSize, Config.BlockSize);
  }
  // プレイヤーの現在地
  ctx!.drawImage(playerIcon, Math.floor(props.playerPosition.x / Config.BlockSize - 1) * 3, Math.floor(props.playerPosition.z / Config.BlockSize - 1) * 3, Config.BlockSize, Config.BlockSize);
  // プレイヤーの現在地を保存
  prevPlayerPosition = props.playerPosition.clone();
};

// watch(() => MapData, () => {
watch(() => props.MapData, () => {
  nextTick(initMapUI);
}, { immediate: true, deep: true });

watch(() => props.playerPosition, () => {
  playerMove();
}, { immediate: true, deep: true });

</script>
  
<style scoped>
/* canvas {
  position: absolute;
  top: 10px;
  left: 10px;
  height: 250px;
} */
</style>
  