import Equipment from './Equipment.ts';

type EquipmentType = 'WEP' | 'ARM' | 'ACC';

export default class Character {
  cha_id: number;
  name: string;
  class: string;
  faceGraphicUrl: string;
  poseGraphicUrl: string;
  LV: number;
  baseStatus: { [key: string]: number };
  equipment: { [key: string]: Equipment | undefined };
  totalStatus: { [key: string]: number };
  nowHP: number;
  nowMP: number;

  constructor(characterData: any, equipmentList: Equipment[]) {
    this.cha_id = characterData.cha_id;
    this.name = characterData.name;
    this.class = characterData.class;
    this.faceGraphicUrl = characterData.faceGraphicUrl;
    this.poseGraphicUrl = characterData.poseGraphicUrl;
    this.LV = characterData.LV;
    this.baseStatus = {
      HP: characterData.HP,
      MP: characterData.MP,
      ATK: characterData.ATK,
      MGC: characterData.MGC,
      DEF: characterData.DEF,
      MDF: characterData.MDF,
      DEX: characterData.DEX,
      SPD: characterData.SPD,
    };
    this.equipment = {
      WEP: equipmentList.find(equipment => equipment.name === characterData.equipment.WEP),
      ARM: equipmentList.find(equipment => equipment.name === characterData.equipment.ARM),
      ACC: equipmentList.find(equipment => equipment.name === characterData.equipment.ACC),
    };
    this.totalStatus = this.calculateTotalStatus();
    this.nowHP = this.totalStatus.HP;
    this.nowMP = this.totalStatus.MP;
  }

  changeEquipment(type: EquipmentType, newEquipment: Equipment | undefined) {
    this.equipment[type] = newEquipment;
  }
  
  calculateTotalStatus() {
    let totalStatus = { ...this.baseStatus };
    for (let key in this.equipment) {
      let equipment = this.equipment[key];
      if (equipment) {  // 装備が存在することを確認
        for (let status in equipment.status) {
          if (totalStatus[status]) {
            totalStatus[status] += equipment.status[status];
          }
        }
      }
    }
    return totalStatus;
  }
}
export interface if_Character extends Character { }