<template>
  <div class="partyUI" v-show="showParty">
    <div v-for="(character, index) in characters" :key="character.cha_id" @click="selectCharacter(index)" class="character-card">
      <StatusUI class="StatusUI" :characters="characters" :index="index"/>
      <img :src="character.poseGraphicUrl" alt="" class="chara-image" />
    </div>
  </div>
  <div v-show="showCharacter">
    <CharacterUI :characters="characters" :index="selectedIndex!"  @toggleParty="toggleParty" />
  </div>
  <IconBack class="IconBack" @click="toggleLayout" v-show="showParty"/>
  <IconParty class="IconParty" @click="toggleLayout"/>
</template>
  
<script setup lang="ts">
import { ref, PropType } from 'vue';

import StatusUI from './Status.vue';
import CharacterUI from './Character.vue';

import IconBack from '@/components/IconBack.vue';
import IconParty from '@/components/IconParty.vue';

import { if_Character } from '@/Class/Character.ts';  // CharacterInterfaceをインポート

const showParty = ref(false);
const showCharacter = ref(false);

const props = defineProps({
  characters: Array as PropType<if_Character[]>,
})

//パーティーアイコンを押して表示切替
const emit = defineEmits(["toggleUIup"])
const toggleLayout = () => {
  emit('toggleUIup', showParty.value)
  showParty.value = !showParty.value;
  showCharacter.value = false;
};

//キャラクターを押して表示切替
const selectedIndex = ref<number | null>(null);
// const selectedIndex = ref(0);
function selectCharacter(index: number) {
  selectedIndex.value = index;
  showParty.value = false;
  showCharacter.value = true;
}
//キャラクター画面から戻る
const toggleParty = () => {
  showCharacter.value = false;
  showParty.value = true;
};

</script>
  
<style scoped>
.partyUI {
  position: absolute;
  top: 1%;
  left: 3%;
  width: 93%;
  /* height: 90%; */
  z-index: 10;
}

.character-card {
  display: inline-block;
  /* height: 90%; */
  width: 25%;
}
.chara-image {
  margin-left: 5%;
  width: 90%;
  /* height: 100px; */
}
.IconBack {
  position: absolute;
  left: 3%;
  bottom: 1%;
}
.IconParty {
  position: absolute;
  right: 10%;
  bottom: 1%;
}
</style>