<template>
  <div v-if="characters && characters[selectedIndex]" class="CharacterUI">
    <div class="Character">
      <IconBack class="IconCharaBack" @click="backCharacter" />
      <img :src="characters[selectedIndex].poseGraphicUrl" alt="" class="chara-image" />
      <IconNext class="IconCharaNext" @click="nextCharacter" />
    </div>
    <StatusUI class="StatusUI" :characters="characters" :index="selectedIndex" />
    <EquipmentUI class="EquipmentUI" :characters="characters" :index="selectedIndex" />
    <IconBack class="Back" @click="toggleLayout" />
  </div>
</template>
  
<script setup lang="ts">
import { ref, PropType, watch } from 'vue';
import StatusUI from './Status.vue';
import EquipmentUI from './Equipment.vue';

import { if_Character } from '@/Class/Character.ts';  // CharacterInterfaceをインポート

import IconBack from '@/components/IconBack.vue';
import IconNext from '@/components/IconNext.vue';

const props = defineProps({
  characters: Array as PropType<if_Character[]>,
  index: { type: Number, default: null },
});

const emit = defineEmits(["toggleParty"])
const toggleLayout = () => {
  emit('toggleParty')
};

const selectedIndex = ref<number>(0);

function nextCharacter() {
  selectedIndex.value = selectedIndex.value == props.characters!.length - 1 ? 0 : selectedIndex.value + 1
}

function backCharacter() {
  selectedIndex.value = selectedIndex.value == 0 ? props.characters!.length - 1 : selectedIndex.value - 1
}

watch(() => props.index, () => {
  selectedIndex.value = props.index;
})

</script>
  
<style scoped>
.Character {
  position: absolute;
  z-index: 15;
  top: 10%;
  left: 5%;
  width: 45%;
  /* height: 100%; */
  display: flex;
  align-items: center;
}

.IconCharaBack {
  width: 10%;
  /* margin-top: 5%; */
  /* display: flex; */
}

.chara-image {
  margin-top: 5%;
  width: 75%;
}

.IconCharaNext {
  width: 8%;
  margin-left: 1%;
}

.StatusUI {
  position: absolute;
  top: 3%;
  left: 44%;
  width: 19%;
  /* height: 100%; */
  padding: 1% 2%;
}

.EquipmentUI {
  position: absolute;
  top: 9%;
  left: 71%;
  width: 22%;
  /* height: 100%; */
}
.Back {
  position: absolute;
  left: 3%;
  bottom: 1%;
}
</style>