<template>
  <MapUI class="MapUI" :MapData="MapData" :playerPosition="playerPosition" />
  <div class="dungeon" ref="dungeon"></div>
</template>

<script setup lang="ts">
import { onMounted, ref } from 'vue';
import * as THREE from 'three';

import MapUI from '../UI/Map.vue';
import Config from '@/config.ts';
import { CreateDungeon, MapData, MapSet, initPoint } from './CreateDungeon.ts';

// 背景インポート
import img_wall01 from '/img/back/wall01.jpg';
import img_floor01 from '/img/back/floor01.jpg';
import img_ceil01 from '/img/back/ceil01.png';
import img_door01 from '/img/back/door01.jpg';

const props = defineProps({
  showUIstatus: Boolean,
})

const dungeon = ref<HTMLElement | null>(null);

let scene = new THREE.Scene()
let camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);
let renderer = new THREE.WebGLRenderer();
let light: THREE.PointLight;
let targetPosition = new THREE.Vector3(); // カメラの目標位置
let targetRotation = 0; // カメラの目標回転角度

const playerPosition = ref(new THREE.Vector3());
// let playerPosition : THREE.Vector3;

const emit = defineEmits(["updatePosition"])
const updatePosition = (newPosition: THREE.Vector3) => {
  emit('updatePosition', newPosition)
}

onMounted(() => {

  //ダンジョン生成
  CreateDungeon()
  console.log(MapData)
  console.log(MapSet)
  // MapRef.value = true
  // MapDataRef.value = MapData
  //シーン初期化
  initScene()
  //ダンジョン描写
  SceneDungeon()
  // キーボードの矢印キーにイベントリスナーを追加します。
  window.addEventListener('keydown', handleKeydown)
  // レンダリングループ
  const animate = function () {
    requestAnimationFrame(animate);
    renderer.render(scene, camera);
  };
  animate();
  gameLoop();

});

function initScene() {
  // renderer.setSize(window.innerWidth - 25, window.innerHeight - 25);
  renderer.setSize(window.innerWidth, window.innerHeight);
  dungeon.value!.appendChild(renderer.domElement);

  // プレイヤーの初期位置を設定
  playerPosition.value = new THREE.Vector3(Config.BlockSize * initPoint.X, 5, Config.BlockSize * initPoint.Y);
  updatePosition(playerPosition.value)
  targetPosition.copy(playerPosition.value);
  // カメラの位置をプレイヤーの位置と同期
  camera.position.copy(playerPosition.value);
  // 上空カメラ
  // camera.position.set(500, 500, 0);
  // camera.lookAt(450, 100, 0);
  //光源を追加
  //全体光源
  // const light = new THREE.AmbientLight(0xFFFFFF, 1.0);  
  //ポイント光源
  // const lightParams = {point: 0xFBFDFF};
  const lightParams = { point: 0xFBF5CB };
  light = new THREE.PointLight(lightParams.point, 15, 150, 1.0);
  light.position.copy(playerPosition.value);
  scene.add(light);
}
// ダンジョンを描画
function SceneDungeon() {
  const loadPic = new THREE.TextureLoader();
  // 壁
  const wGeometry = new THREE.PlaneGeometry(Config.BlockSize, Config.BlockHeight);
  const wTexture = loadPic.load(img_wall01);
  const wMaterial = new THREE.MeshPhongMaterial({ map: wTexture, side: THREE.DoubleSide, bumpMap: wTexture, bumpScale: 0.2 });

  const dTexture = loadPic.load(img_door01);
  const dMaterial = new THREE.MeshPhongMaterial({ map: dTexture, side: THREE.DoubleSide, bumpMap: dTexture, bumpScale: 0.2 });

  // 床
  const fGeometry = new THREE.PlaneGeometry(Config.BlockSize, Config.BlockSize);
  const fTexture = loadPic.load(img_floor01);
  const fMaterial = new THREE.MeshPhongMaterial({ map: fTexture, side: THREE.DoubleSide, bumpMap: fTexture, bumpScale: 0.2 });
  // 天井
  // var uGeometry = new THREE.PlaneGeometry(BLOCK_SIZE, BLOCK_SIZE);
  const cTexture = loadPic.load(img_ceil01);
  const cMaterial = new THREE.MeshPhongMaterial({ map: cTexture, side: THREE.DoubleSide, bumpMap: cTexture, bumpScale: 0.2 });

  function isWall(y: number, x: number) {
    return y >= 0 && y < MapData.length && x >= 0 && x < MapData[0].length && MapData[y][x] == Config.MapWall;
  }

  // function isRoom(y: number, x: number) {
  //   return y >= 0 && y < Map.length && x >= 0 && x < Map[0].length && (Map[y][x] == MapRoom);
  // }

  // function isRoad(y: number, x: number) {
  //   return y >= 0 && y < Map.length && x >= 0 && x < Map[0].length && (Map[y][x] == MapRoad);
  // }

  for (let i = 0; i < MapData.length; i++) {
    for (let j = 0; j < MapData[i].length; j++) {

      if (MapData[i][j] == Config.MapRoom || MapData[i][j] == Config.MapRoad) {

        // 部屋か通路の場合、床と天井を追加
        const planeFloor = new THREE.Mesh(fGeometry, fMaterial);
        planeFloor.position.set(Config.BlockSize * j, 0, Config.BlockSize * i);
        planeFloor.rotation.x = 90 * Math.PI / 180;
        scene.add(planeFloor);

        // 天井はマップが出来てから設置
        // const planeCeil = new THREE.Mesh(fGeometry, cMaterial);
        // planeCeil.position.set(Config.BlockSize * j, Config.BlockHeight, Config.BlockSize * i);
        // planeCeil.rotation.x = 90 * Math.PI / 180;
        // scene.add(planeCeil);

        // 通路や部屋の上下左右で壁がある場合に壁を描画
        if (isWall(i - 1, j)) {
          const planeWall = new THREE.Mesh(wGeometry, wMaterial);
          planeWall.position.set(Config.BlockSize * j, Config.BlockHeight / 2, Config.BlockSize * (i - 0.5));
          scene.add(planeWall);
          // walls.push(planeWall);
        }
        if (isWall(i + 1, j)) {
          const planeWall = new THREE.Mesh(wGeometry, wMaterial);
          planeWall.position.set(Config.BlockSize * j, Config.BlockHeight / 2, Config.BlockSize * (i + 0.5));
          scene.add(planeWall);
          // walls.push(planeWall);
        }
        if (isWall(i, j - 1)) {
          const planeWall = new THREE.Mesh(wGeometry, wMaterial);
          planeWall.position.set(Config.BlockSize * (j - 0.5), Config.BlockHeight / 2, Config.BlockSize * i);
          planeWall.rotation.y = 90 * Math.PI / 180;
          scene.add(planeWall);
          // walls.push(planeWall);
        }
        if (isWall(i, j + 1)) {
          const planeWall = new THREE.Mesh(wGeometry, wMaterial);
          planeWall.position.set(Config.BlockSize * (j + 0.5), Config.BlockHeight / 2, Config.BlockSize * i);
          planeWall.rotation.y = 90 * Math.PI / 180;
          scene.add(planeWall);
          // walls.push(planeWall);
        }

        // 通路と部屋の境目に扉を描画
        const planeDoor = new THREE.Mesh(wGeometry, dMaterial);
        switch (MapSet[i][j]) {
          case Config.SetDoorUp:
            //この地点か一つ上の左右が壁の場合に描画
            if ((isWall(i, j + 1) && isWall(i, j - 1)) || (isWall(i - 1, j + 1) && isWall(i - 1, j - 1))) {
              planeDoor.position.set(Config.BlockSize * j, Config.BlockHeight / 2, Config.BlockSize * (i - 0.5));
              scene.add(planeDoor);
            }
            break
          case Config.SetDoorUnder:
            //この地点か一つ下の左右が壁の場合に描画
            if ((isWall(i, j + 1) && isWall(i, j - 1)) || (isWall(i + 1, j + 1) && isWall(i + 1, j - 1))) {
              planeDoor.position.set(Config.BlockSize * j, Config.BlockHeight / 2, Config.BlockSize * (i + 0.5));
              scene.add(planeDoor);
            }
            break
          case Config.SetDoorLeft:
            //この地点か一つ左の上下が壁の場合に描画
            if ((isWall(i + 1, j) && isWall(i - 1, j)) || (isWall(i + 1, j - 1) && isWall(i - 1, j - 1))) {
              planeDoor.position.set(Config.BlockSize * (j - 0.5), Config.BlockHeight / 2, Config.BlockSize * i);
              planeDoor.rotation.y = 90 * Math.PI / 180;
              scene.add(planeDoor);
            }
            break
          case Config.SetDoorRight:
            //この地点か一つ右の上下が壁の場合に描画
            if ((isWall(i + 1, j) && isWall(i - 1, j)) || (isWall(i + 1, j + 1) && isWall(i - 1, j + 1))) {
              planeDoor.position.set(Config.BlockSize * (j + 0.5), Config.BlockHeight / 2, Config.BlockSize * i);
              planeDoor.rotation.y = 90 * Math.PI / 180;
              scene.add(planeDoor);
            }
            break
          default:
        }

      }

    }
  }
}

//十字キーでの移動
const handleKeydown = (event: KeyboardEvent) => {
  if (!props.showUIstatus) return;
  if (!camera) return
  const direction = new THREE.Vector3()
  camera.getWorldDirection(direction)
  let newPosition = playerPosition.value.clone();

  switch (event.key) {
    case 'ArrowUp':
      // 上矢印キーが押されたときに前に進む
      newPosition.add(direction.multiplyScalar(Config.BlockSize));
      correctPosition()
      break
    case 'ArrowDown':
      // 下矢印キーが押されたときに後ろに進む
      newPosition.sub(direction.multiplyScalar(Config.BlockSize));
      correctPosition()
      break
    case 'ArrowLeft':
      // 左矢印キーが押されたときに左に進む
      newPosition.x += direction.z * (Config.BlockSize)
      newPosition.z -= direction.x * (Config.BlockSize)
      correctPosition()
      break
    case 'ArrowRight':
      // 右矢印キーが押されたときに右に進む
      newPosition.x -= direction.z * (Config.BlockSize)
      newPosition.z += direction.x * (Config.BlockSize)
      correctPosition()
      break
    case 'a':
    case 'A':
      // Aキーが押されたときに視点を左に90度回転
      targetRotation += Math.PI / 2; // 目標回転角度を更新
      break
    case 'd':
    case 'D':
      // Dキーが押されたときに視点を右に90度回転
      targetRotation -= Math.PI / 2; // 目標回転角度を更新
      break
  }

  function correctPosition() {
    newPosition.x = Math.round(newPosition.x / Config.BlockSize) * Config.BlockSize;
    newPosition.z = Math.round(newPosition.z / Config.BlockSize) * Config.BlockSize;
  }

  // 新しい位置が壁でないことを確認
  let newX = Math.floor((newPosition.x + 5) / Config.BlockSize);
  let newY = Math.floor((newPosition.z + 5) / Config.BlockSize);
  console.log(newPosition.x, newX, newPosition.z, newY)
  // 
  if (MapData[newY][newX] !== 0) {
    // 新しい位置が壁でなければ、プレイヤーの位置を更新
    // カメラの位置をプレイヤーの位置と同期
    playerPosition.value.copy(newPosition);
    updatePosition(newPosition)
    targetPosition.copy(newPosition); // 目標位置を新しい位置に更新
  }
}

const gameLoop = () => {
  // カメラの位置を更新
  if (!camera.position.equals(targetPosition)) {
    camera.position.lerp(targetPosition, 0.05);
    light.position.copy(targetPosition);
  }

  // カメラの回転を更新
  if (camera.rotation.y !== targetRotation) {
    camera.rotation.y += (targetRotation - camera.rotation.y) * 0.10;
  }

  requestAnimationFrame(gameLoop); // 次のフレームでゲームループを再度呼び出す
}

</script>

<style scoped>
/* .dungeon {
  height: 100%;
  width: auto;
} */
.MapUI {
  position: absolute;
  height: 30%;
}
</style>